
@import url("https://fonts.googleapis.com/css?family=Merriweather|Merriweather+Sans:400,700");
.about {
  position: relative;
  color: #1a1a1a;
  background-color: #e6e6e6;
  padding-bottom: 0;
}
.about + section {
  margin-top: 0;
}
.about + section > *:first-child {
  padding-top: 1em;
}
.about__name {
  margin: 0;
}
.about__title {
  margin-top: 0;
}
.about .social-icons {
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%) translateY(50%);
  z-index: 5;
}
.h-card {
  position: relative;
  opacity: .85;
  padding-bottom: 5em;
  z-index: 3;
}
.transition-shape {
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  transform: translateY(100%);
}
.trapezoid {
  border-top: 4em solid #e6e6e6;
  border-left: 5em solid transparent;
  border-right: 5em solid transparent;
  height: 0;
  width: 60%;
}
.trapezoid:first-child,
.trapezoid:last-child {
  width: calc(40% / 2);
}
.trapezoid:first-child:before {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  width: 0;
  height: 0;
  border-bottom: 4em solid #e6e6e6;
  border-right: 5em solid #e6e6e6;
}
.trapezoid:last-child:after {
  content: '';
  display: block;
  position: absolute;
  right: 0;
  top: 0;
  width: 0;
  height: 0;
  border-bottom: 4em solid #e6e6e6;
  border-left: 5em solid #e6e6e6;
}



