
@import url("https://fonts.googleapis.com/css?family=Merriweather|Merriweather+Sans:400,700");
body {
  position: relative;
  margin: 0;
  padding: 0;
  font-family: "Merriweather", serif;
}
h1, h2, p, a {
  line-height: 1.48;
}
h1, h2 {
  font-family: "Merriweather Sans", sans-serif;
  text-transform: uppercase;
  margin-top: 0;
}
* + h1, * + h2 {
  margin-top: 1em;
}
h1 {
  font-size: 3rem;
  line-height: 1.3;
  margin-bottom: .5em;
}
h2 {
  font-size: 2.5rem;
  margin-bottom: .5em;
}
h3 {
  margin-top: 2em;
}
p, a {
  font-size: 1rem;
}
a {
  color: #399aea;
}
a:hover {
  color: #8766db;
}
a:visited {
  color: #a0b4d2;
}
p {
  line-height: 1.8;
  text-indent: 2em;
}
p + p {
  margin-top: 1em;
}
td {
  padding: .5em 1em;
}
main {
  position: relative;
}
@media screen and (min-width: 1px) {
main {
      width: 100vw;
}
}
@media screen and (min-width: 768px) {
main {
      width: 80vw;
}
}
@media screen and (min-width: 1024px) {
main {
      width: 85vw;
}
}
section {
  max-width: 100%;
}
section + section {
  margin-top: 5em;
}
section:first-child {
  padding-top: 3em;
}
@media screen and (min-width: 1px) {
section {
    padding: 1em;
}
}
@media screen and (min-width: 768px) {
section {
    padding-left: 5vw;
    padding-right: 5vw;
}
}
@media screen and (min-width: 1366px) {
section {
    padding-left: 20vw;
    padding-right: 20vw;
}
}
button,
[role=button],
a[role=button] {
  display: block;
  width: calc(100% - 2em);
  color: #e6e6e6;
  background-color: transparent;
  border: 3px solid #e6e6e6;
  padding: 1em;
  margin: 0 auto;
  border-radius: 10em;
  font-weight: bold;
  font-size: 1.5em;
  font-family: "Merriweather Sans", sans-serif;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  cursor: pointer;
  transition: color .2s, background-color .2s;
}
button:hover, button:focus,
  [role=button]:hover,
  [role=button]:focus,
  a[role=button]:hover,
  a[role=button]:focus {
    color: #e6e6e6;
    background-color: #8766db;
    border-color: #8766db;
    outline: none;
}
button.--minor,
  [role=button].--minor,
  a[role=button].--minor {
    background-color: #1a1a1a;
    color: #e6e6e6;
    border: none;
    width: calc(50% - 2em);
}
button.--close,
  [role=button].--close,
  a[role=button].--close {
    color: #e6e6e6;
    background-color: #1a1a1a;
    width: 2em;
    height: 2em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    padding: 0;
    -ms-flex-align: center;
        align-items: center;
    font-size: 1em;
    border: none;
    line-height: 1;
}
button.--close:hover,
    [role=button].--close:hover,
    a[role=button].--close:hover {
      transform: rotate(360deg);
      transition: transform .5s ease;
}
* + [role=button],
* + a[role=button] {
  margin-top: 2em;
}
[role=grid] {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
}
[role=gridcell] {
  margin: .05em;
}
a[role=gridcell] {
  text-decoration: none;
  color: inherit;
}

/* UTILITIES */
.flex-row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row !important;
      flex-direction: row !important;
}
.flex-col {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.flex-center {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.flex-center-row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}
.flex-center-col {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}
.flex-right {
  margin-left: auto;
  margin-right: 0;
}
.content-center {
  -ms-flex-pack: center;
      justify-content: center;
}
.items-center {
  -ms-flex-align: center;
      align-items: center;
}
.text-center {
  text-align: center !important;
}



